@import "~styles/variables";  // '~' informs sass-loader that is not relative path


//styles for footer section
.footer {
  height: 80px;
  background-color: #22272b;
  color: #ffffff;
  text-transform: uppercase;
  display: flex;
  flex-wrap: nowrap;
  align-items: center; }

.footer__container {
  width: 50%; }

.container__social {
  display: flex;
  justify-content: flex-start; }

.container__copy-right {
  display: flex;
  justify-content: flex-end; }

.container__copy-right-text {
  margin-right: 70px;
  color: #a7a9ac;
  letter-spacing: 1px; }

.footer__social-icons {
  list-style: none;
  display: inline-block;
  padding: 0;
  margin-left: 70px; }



.footer__social-icons__elements:first-child {
  border-right: 1px solid #a7a9ac;
  padding-right: 10px;
  margin-right: 10px;
  color: #a7a9ac; }


.footer__social-icons__elements {
  display: inline;
  margin-right: 20px;
  font-weight: 400;
  letter-spacing: 1px; }

